import React, { useEffect, useState, useMemo } from 'react';
import Papa from 'papaparse';
import './App.css';

function MatchMessages({ dataType, onDataTypeChange }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchData = async () => {
    setLoading(true);
    let gid = '368108442'; // GID for all matches

    const publicSpreadsheetUrl = `https://docs.google.com/spreadsheets/d/e/2PACX-1vRojRhLgZSPXJopPdni1V4Z-inXXY3a__2NaVMsoJHPs9d25ZQ7t56QX67mncr6yo-w4B8WCWyHFe2m/pub?output=csv&gid=${gid}`;
    const cacheBuster = `cacheBuster=${new Date().getTime()}`;
    const urlWithCacheBuster = `${publicSpreadsheetUrl}&${cacheBuster}`;

    try {
      Papa.parse(urlWithCacheBuster, {
        download: true,
        header: true,
        complete: function (results) {
          setData(results.data);
          setLoading(false);
        },
        error: function (error) {
          console.warn('Error fetching data from Google Sheets', error);
          setLoading(false);
        },
      });
    } catch (error) {
      console.error('Fetching data failed', error);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
    const intervalId = setInterval(() => fetchData(), 60000); // Refresh every 60 seconds

    return () => clearInterval(intervalId);
  }, [dataType]);

  const calculatePerformanceChange = useMemo(() => (teamMatches) => {
    if (teamMatches.length < 2) return 'Insufficient data';

    const firstMatch = teamMatches[0];
    const lastMatch = teamMatches[teamMatches.length - 1];

    const performanceMetrics = ['AMP AUTO', 'SPEAKER AUTO', 'mid notes', 'tele AMP', 'Missed AMP', 'tele Speaker', 'tele Missed Speaker', 'Defensive Pins', 'Shot to Trap', 'Under Chain', 'Long Shot'];
    const changes = performanceMetrics.map(metric => ({
      metric,
      change: lastMatch[metric] - firstMatch[metric],
    }));

    const summary = changes.map(change => {
      if (change.change > 0) return `${change.metric} improved by ${change.change}`;
      if (change.change < 0) return `${change.metric} worsened by ${Math.abs(change.change)}`;
      return `${change.metric} remained the same`;
    });

    return summary.join(', ');
  }, []);

  return (
    <div className="match-messages-container">
      {loading && (
        <div className="spinner-container">
          <div className="spinner"></div>
        </div>
      )}
      {data.slice().reverse().map((match, index) => ( // Reverse the data array before mapping
        <div key={index} className="match-message">
          <h3>Match {match['Match Number']} - Team {match['Teams']}</h3>
          <p><strong>AMP AUTO:</strong> {match['AMP AUTO']}</p>
          <p><strong>SPEAKER AUTO:</strong> {match['SPEAKER AUTO']}</p>
          <p><strong>Mid Notes:</strong> {match['mid notes']}</p>
          <p><strong>Tele AMP:</strong> {match['tele AMP']}</p>
          <p><strong>Missed AMP:</strong> {match['Missed AMP']}</p>
          <p><strong>Tele Speaker:</strong> {match['tele Speaker']}</p>
          <p><strong>Tele Missed Speaker:</strong> {match['tele Missed Speaker']}</p>
          <p><strong>Defensive Pins:</strong> {match['Defensive Pins']}</p>
          <p><strong>Shot to Trap:</strong> {match['Shot to Trap']}</p>
          <p><strong>Under Chain:</strong> {match['Under Chain']}</p>
          <p><strong>Long Shot:</strong> {match['Long Shot']}</p>
          <p><strong>Map:</strong> {match['map']}</p>
          <p><strong>Performance Summary:</strong> {calculatePerformanceChange(data.filter(m => m['Teams'] === match['Teams']))}</p>
        </div>
      ))}
    </div>
  );
}

export default MatchMessages;
